.gallery {
	display: flex;
	flex-wrap: wrap;
}

.gallery-item {
	padding: 4px;
	margin-bottom: 8px;
}

.gallery-icon {
	a {
		text-decoration: none;
	}
}

.gallery-columns-2 {
	.gallery-item {
		width: (100% / 2);
	}
}

