[type="text"],
[type="search"],
[type="tel"],
[type="url"],
[type="email"],
[type="password"],
textarea {
  padding: 8px;
  border: 1px solid #ccc;
  border-radius: 0;
  background-image: none;
  font-family: inherit;
  font-size: 1em;
}

[type="text"]:hover,
[type="search"]:hover,
[type="tel"]:hover,
[type="url"]:hover,
[type="email"]:hover,
[type="password"]:hover,
textarea:hover {
  outline: none;
}

[type="text"]:focus,
[type="search"]:focus,
[type="tel"]:focus,
[type="url"]:focus,
[type="email"]:focus,
[type="password"]:focus,
textarea:focus {
  border-color: #ccc;
}

button,
[type="button"],
[type="submit"] {
  width: auto;
  display: inline-block;
  padding: 0.6em 2em;
  margin: 0;
  background-image: none;
  background-color: #666;
  color: #fff;
  text-decoration: none;
  border: none;
  border-radius: 0;
  font-size: 16px;
  cursor: pointer;
  transition: opacity 0.3s ease 0s;
}

button:hover,
[type="button"]:hover,
[type="submit"]:hover {
  outline: none;
  opacity: 0.7;
}

[type="radio"] {
  display: none;
}

[type="radio"] + span {
  display: inline-block;
  position: relative;
  padding: 0 0 0 30px;
  margin: 0 16px 0 0;
  cursor: pointer;
}

[type="radio"] + span::before {
  position: absolute;
  content: "";
  top: 50%;
  left: 0;
  transform: translateY(-50%);
  background: #fff;
  border: 1px solid #ccc;
  border-radius: 50%;
  width: 26px;
  height: 26px;
  display: block;
}

[type="radio"] + span::after {
  opacity: 0;
  transition: opacity 0.3s ease 0s;
}

[type="radio"]:checked + span::after {
  position: absolute;
  content: "";
  top: 50%;
  left: 8px;
  transform: translateY(-50%);
  background: #ccc;
  border-radius: 50%;
  width: 10px;
  height: 10px;
  display: block;
  opacity: 1;
}

[type="checkbox"] {
  display: none;
}

[type="checkbox"] + span {
  display: inline-block;
  position: relative;
  padding: 0 0 0 30px;
  margin: 0 16px 0 0;
  cursor: pointer;
  transition: all 1s ease 0s;
}

[type="checkbox"] + span::before {
  position: absolute;
  content: "";
  top: 50%;
  left: 0;
  transform: translateY(-50%);
  background: #fff;
  border: 1px solid #ccc;
  border-radius: 2px;
  width: 26px;
  height: 26px;
  display: block;
}

[type="checkbox"] + span::after {
  opacity: 0;
  transition: opacity 0.3s ease 0s;
}

[type="checkbox"]:checked + span::after {
  position: absolute;
  content: "";
  top: 50%;
  left: 0;
  transform: translateY(-50%) rotate(-45deg);
  width: 26px;
  height: 15.6px;
  margin-top: -0.2em;
  display: block;
  border-left: 4px solid #666;
  border-bottom: 4px solid #666;
  opacity: 1;
}

select {
  padding: 0.4em 2.4em 0.4em 0.8em;
  border-radius: 0;
  font-family: inherit;
  font-size: 1em;
  color: inherit;
  cursor: pointer;
  border: 1px solid #ccc;
}

select::-ms-expand {
  display: none;
}

select:focus {
  border-color: #ccc;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

